.JokeList {
	display: flex;
	width: 80%;
	height: 80%;
}

.JokeList-sidebar {
	background: #9575cd;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	width: 30%;
	z-index: 2;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1), inset 0 0 25px #7e57c2;
}

.JokeList-title {
	font-size: 4rem;
	margin: 2rem;
	color: white;
	font-weight: 300;
	letter-spacing: 0.6rem;
}

.JokeList-title span {
	font-weight: 800;
	letter-spacing: 0;
}

.JokeList-sidebar img {
	width: 50%;
	border-radius: 50%;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokeList-jokes {
	height: 90%;
	background: white;
	align-self: center;
	width: 70%;
	overflow: scroll;
	overflow-x: auto;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.spinner {
	color: white;
	font-weight: 100;
	letter-spacing: 0.6rem;
	text-align: center;
}

.JokeList-getmore {
	margin: 1rem 0;
	font-size: 1.5rem;
	border-radius: 2rem;
	padding: 1rem;
	width: 50%;
	color: white;
	border: none;
	outline: none;
	cursor: pointer;
	transition: 0.8s cubic-bezier(0.2 , 1, 0.20, 1);
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
	background: linear-gradient(
		135deg,
		rgba(179, 229, 252, 1)0%,
		rgba(179, 229, 252, 1)50%,
		rgba(240, 98, 146, 1)50%,
		rgba(240, 98, 146, 1)100%
	);
}

.JokeList-getmore:hover {
	transform: translateY(-2px);
	box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
}
